import axios from 'axios';
import React,{useState,useContext, useEffect} from 'react'
import {Score} from "../App"
import PlayerInfo from "./PlayerInfo"
export default function Game() {
    const [fliping, setfliping] = useState(false);
    const [all, setall] = useState([])
    const [record, setrecord] = useState([])
    const [number, setnumber] = useState(0) 
    const [stats, setstats] = useState(true)
    const [mLB, setmLB] = useState(false)
    const [mR, setmR] = useState(false)
    const players = useContext(Score)
    
    const {player,setplayer,logged,setlogged } = players

    useEffect(async() => {
      const get = () => {
           new Promise((resolve)=>{
           setTimeout(async() => {
           const {data} = await axios.get(`https://gentle-dusk-38020.herokuapp.com/login`)
           setall(data)
           resolve()
          }, 2000);
        })
      }
      get()
    }, [fliping])

    const logout = () => {
      setplayer([])
      setlogged(false)
    }
    const next = () => {
      if (number >= record.length - 1) {
        setnumber(0)
      } else setnumber(number + 1)
    }
    const prev = () => {
      console.log(record.length);
      console.log(4);
      if (number <= 0) {
        setnumber(record.length - 1)
        
      } else setnumber(number - 1)
    }

    const flip = async(e,roll) => {
      e.preventDefault()
      const head = document.querySelector(".Heads")
      const tail = document.querySelector(".Tails")
      const face = document.querySelector(".coin")
      face.textContent=""
      setfliping(true)
      head.style.pointerEvents = "none" 
      tail.style.pointerEvents = "none"
      await new Promise((resolve)=>{
         setTimeout (()=>{
        setfliping(false)
        head.style.pointerEvents = "auto"
        tail.style.pointerEvents = "auto"
        resolve()
       },2000)})
      
      const num = Math.floor(Math.random() * 2);
      const percent = (wins,games) => {
        const total = Math.ceil((wins/games)*100 )  
        return total
        }
      if (roll === num) {
          const victory = {
            coloring:"green",
            status:"W"
          }
          face.textContent = victory.status + "in"
          setrecord([...record,victory])
         await axios.patch(`https://gentle-dusk-38020.herokuapp.com/login/query?_id=${player[0]._id}`,{games:player[0].games++,wins:player[0].wins++,percent:percent(player[0].wins,player[0].games)})
      } else {
        const defeat = {
            coloring:"red",
            status:"L"
          }
          face.textContent = defeat.status + "oss"
          setrecord([...record,defeat])
        await axios.patch(`https://gentle-dusk-38020.herokuapp.com/login/query?_id=${player[0]._id}`,{games:player[0].games++,percent:percent(player[0].wins,player[0].games)})
      }; 
    }
    return (
        <section className="Game" >
          <nav className="top">
            <div className="userInfo">
              <h1 className="user" onClick={()=>setstats(!stats)} >
                <div className="name">Hello, {player[0].username}</div>
                { stats &&
              <> 
              <h1 className="Wins" >Wins:{player[0].wins}</h1>
              <h1 className="Games" >Games:{player[0].games}</h1>
              </>
              }
              </h1>
            </div>
            
            <button className="logout" onClick={()=>logout()} >logout</button>
          </nav>
           <article className="mid">
             {window.innerWidth <= `600` ? 
             <>
              <button className="MR"  onClick={()=>setmR(true)}>Record</button>
              {mR && 
              <div className="backscreen" >
                <button className="exit" onClick={()=>setmR(false)} >X</button>
               <div className="record">
                <h1 className="title">Record</h1>
                <div className="records"   >
                  <button className="back" onClick={()=>prev()} > - </button>
                  { record.length > 0 && <div style={{color:record[number].coloring}}> {number + 1}:{record[number].status}</div>}
                  <button className="forward" onClick={()=>next()} > + </button> 
               </div>
             </div>
             </div> }
             </> 
             : 
             <div className="record">
                <h1 className="title">Record</h1>
                <div className="records"   >
                  <button className="back" onClick={()=>prev()} > - </button>
                  { record.length > 0 && <div style={{color:record[number].coloring}}> {number + 1}:{record[number].status}</div>}
                  <button className="forward" onClick={()=>next()} > + </button> 
               </div>
             </div> }
             
             <div className="Coin">      
              <div className={`coin ${fliping ? `flip` :"test2"} `}></div>
            </div>
            {window.innerWidth <= "600" ? 
            <>
            <button className="MLB"  onClick={()=>setmLB(true)} >Leaderboard</button>
              {mLB && 
              <div className="backscreen" >
                <button className="exit" onClick={()=>setmLB(false)}  >X</button>
               <div className="LB">
              <h1 className="leaderboard" >Leaderboard</h1>
                {all.map((info,index,array)=>{
                const { games,wins,username,percent } = info
                return (
                  <PlayerInfo games={games} wins={wins} username={username} percent={percent} />
                )
                })}
            </div>
            </div> }
            </> 
            :
            <div className="LB">
              <h1 className="leaderboard" >Leaderboard</h1>
                {all.map((info,index,array)=>{
                const { games,wins,username,percent } = info
                return (
                  <PlayerInfo games={games} wins={wins} username={username} percent={percent} />
                )
                })}
            </div> }
            
           </article>
            <article className="bottom" >
              <div className="buttons">
                <button className="Heads" onClick={(e)=>flip(e,1)} >Heads</button>
                <button className="Tails" onClick={(e)=>flip(e,0)} >Tails</button>
              </div>
            </article>
        </section>
    )
}
